<template>
    <section>
        <h1 @click="flodAll()" id="top"><el-icon>
                <Star color="red"/>
            </el-icon>《主题》</h1>
        <hr />
        <section>
            <el-row :gutter="20">
                <el-col :span="20">
                    <el-timeline>
                        <el-timeline-item v-for="event, index in events" :key="index" :timestamp="event.date" color="gray"
                            placement="top" size="large">
                            <el-card>
                                <h2 @click="flod(event)">{{ event.title }}</h2>
                                <Transition name="slide-fade">
                                    <div v-show="event.show">
                                        <h3>事件概述</h3>
                                        <div>&ensp;&ensp;&ensp;&ensp;{{ event.description }}</div>
                                        <h3>意义</h3>
                                        <div v-show="event.meaning != []" v-for="meaning, index in event.meaning"
                                            :key="index">
                                            &ensp;&ensp;{{ index + 1 }}. {{ meaning }}
                                        </div>
                                        <div align="center" v-show="event.img != ''">
                                            <img :src="event.src" style="width: 60%;">
                                        </div>
                                    </div>
                                </Transition>
                            </el-card>
                        </el-timeline-item>
                    </el-timeline>
                </el-col>
            </el-row>
        </section>
    </section>
    <hr />
    <div class="to-top-button">
        <a href="#top" style="color: black; display: inline-block; width: 80%; height: 80%;">
            <el-icon :size="50">
                <ArrowUpBold style="margin-top: 30%; vertical-align: middle;"/>
            </el-icon>
        </a>
    </div>
</template>

<script>
import { ref } from 'vue'
export default {
    name: 'TimeLine',
    setup() {
        function flod(event) {
            event.show = !event.show;
        }
        function flodAll() {
            for (let i = 0; i < events.value.length; i++) {
                events.value[i].show = false;
            }
        }
        let events = ref([
            {
                title: '太平天国运动',
                date: '1851-1864',
                description: '太平天国运动是清朝咸丰元年到同治三年（1851年—1864年）期间，由洪秀全、杨秀清、萧朝贵、冯云山、韦昌辉、石达开等组成的领导集团从广西金田村率先发起的反对清朝封建统治和外国资本主义侵略的农民起义战争，是19世纪中叶中国最大的一场大规模反清运动。1864年，随着太平天国首都天京（南京）的陷落，标志着运动失败。',
                meaning: [
                    '洪秀全领导的太平天国运动，体现了新时代农民起义战争的特点', '太平天国的一些领导人，开始向西方寻求真理，探索中国独立、富强的途径，勇敢地担负起反封建、反侵略的任务。太平天国运动是中国农民起义战争的高峰。'
                ],
                show: true
            },
            {
                title: '戊戌变法',
                date: '1898/6/11-1898/921',
                description: '戊戌变法，又称百日维新、维新变法、维新运动，是晚清时期以康有为、梁启超为代表的维新派人士通过光绪帝进行倡导学习西方，提倡科学文化，改革政治、教育制度，发展农、工、商业等的资产阶级改良运动。',
                meaning: [
                    '戊戌变法是一次具有爱国救亡意义的变法维新运动，是中国近代史上一次重要的政治改革，也是一次思想启蒙运动，这次变法促进了思想解放，并且对思想文化的发展和促进中国近代社会的进步起了重要推动作用。'
                ],
                show: false
            },
            {
                title: '辛亥革命',
                date: '1905-03-25',
                description: '辛亥革命是指发生于中国农历辛亥年（清宣统三年），即公元1911年至1912年初，旨在推翻清朝专制帝制、建立共和政体的全国性革命。',
                meaning: [
                    '辛亥革命的发生，是近代以来中国社会矛盾激化和中国人民顽强斗争的必然结果', '它在政治上、思想上给中国人民带来了不可低估的解放作用',
                    '开创了完全意义上的近代民族民主革命，推翻了统治中国几千年的君主专制制度，建立起共和政体，结束君主专制制度。', '传播了民主共和理念，极大推动了中华民族思想解放，以巨大的震撼力和影响力推动了中国社会变革。'
                ],
                show: false
            },
            {
                title: '五四运动',
                date: '1919-05-04',
                description: '5月4日，五四运动爆发。',
                meaning: [
                    '五四运动是一场伟大的群众爱国运动。它的斗争对象直指帝国主义和北洋军阀政府，表现出的反帝反封建的彻底性是史五四运动上前所未有的。它充分发动了群众，工、商、学联合起来，农民也有部分参加了，实际上揭开了全民族进行彻底的反帝反封建斗争的序幕。',
                    '五四运动是一场深刻的思想解放运动。它使中国人民进一步认识到帝国主义侵略的本质和军阀统治的黑暗，同时进一步提高了中国人民反帝反封建的决心和觉悟；促进了全国人民对改造中国的问题的反思和探索，也促进了新思潮的蓬勃兴起和马克思主义的传播。',
                    '五四运动既揭开了新民主主义革命的序幕，又开创了中国新民主主义革命的开端。从此，无产阶级登上了政治舞台，民众的力量得到了广泛的发动等，这些不仅使五四运动本身具有新民主主义革命的基本内涵，还直接为中国共产党的成立创造了阶级上、思想上和干部上的条件。'
                ],
                src: require('../assets/img/54.webp'),
                show: false
            },
            {
                title: '第一次国共合作',
                date: '1924-1927',
                description: '第一次国共合作，即在大革命时期，中国共产党同中国国民党首次建立的合作。',
                meaning: [
                    '中共帮助国民党组建省、市地方党部。许多共产党员担任国民党省市级党部负责人。',
                    '中共和共产国际帮助国民党创建黄埔军校和国民革命军，为革命军队培养造就了一批军政人才。',
                    '工农运动得到大力开展。中共领导了广州沙面罢工和海陆丰农民运动，并且帮助国民党在广州创办了六届农民运动讲习所。',
                    '1925年1月，中共四大召开，首次明确提出无产阶级领导权和农民同盟军问题。中共四大以后，全国掀起国民会议运动、五卅运动和省港大罢工，把革命推向高潮。',
                    '长期沉寂的北方革命运动得到前所未有的发展。'
                ],
                show: false
            },
            {
                title: '八一南昌起义',
                date: '1927-08-01',
                description: '中国共产党领导部分国民革命军在江西省南昌市举行的武装起义。起义由周恩来、贺龙、李立三、叶挺、朱德、刘伯承、谭平山领导。',
                meaning: [
                    '打响了武装反抗国民党反动派的第一枪，揭开了中国共产党独立领导武装斗争和创建革命军队的序幕。'
                ],
                show: false
            },
            {
                title: '秋收起义',
                date: '1927',
                description: '土地革命战争时期，中国共产党在湖南、江西两省边界地区领导农民自卫军、工人纠察队和革命士兵举行的武装起义。',
                meaning: [
                    '湘赣边界秋收起义，第一次在武装斗争中公开打出中国共产党的旗帜，进一步在全国人民面前表明了中国共产党独立领导革命战争的决心',
                    '走上了在农村开展游击战争，建立革命根据地，以保存和发展革命力量的道路', '秋收起义中组成的工农革命军，后来成为中国工农红军的基本骨干之一'
                ],
                show: false
            },
            {
                title: '九一八事变',
                date: '1931-09-18',
                description: '九·一八事变，又称奉天事变、柳条湖事件。 是1931年9月18日日本驻中国东北地区的关东军突然袭击沈阳，以武力侵占东北的事件。',
                meaning: [
                    '九·一八事变是由日本蓄意制造并发动的侵华战争，是日本帝国主义企图以武力征服中国的开端，是中国抗日战争的起点，标志着中国局部抗战的开始，揭开了第二次世界大战东方战场的序幕',
                    '九一八事变后，中国人民的局部抗战也标志着世界反法西斯战争的起点。'
                ],
                show: false
            },
            {
                title: '卢沟桥事变',
                date: '1937-07-07',
                description: '七七事变，又称卢沟桥事变，发生于1937年7月7日',
                meaning: [
                    '“卢沟桥事变”揭开了全国抗日战争的序幕'
                ],
                show: false
            },
        ]);
        return {
            flod,
            flodAll,
            events,
        };
    },
}
</script>

<style scoped>

h1 {
    text-decoration: underline;
    text-decoration-color: black;
    text-underline-offset: 10px;
}

h2 {
    text-decoration: underline;
    text-decoration-color: black;
    text-underline-offset: 10px;
}

h2::before {
    content: "事记";
    display: inline-block;
    color: gold;
    font-size: 30px;
    align-items: center;
    font-family: kai;
    background-color: red;
    width: 62px;
    height: 45px;
    border-radius: 10px;
}

h3,
h4 {
    font-family: kai;
}

/*
  进入和离开动画可以使用不同
  持续时间和速度曲线。
*/
.slide-fade-enter-active {
    transition: all 0.8s ease-in;
}

.slide-fade-leave-active {
    transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter-from,
.slide-fade-leave-to {
    transform: translateX(20px);
    opacity: 0;
}

.to-top-button{
    position: fixed;
    bottom: 5%;
    right: 5%;
    z-index: 999;
    height: 70px;
    width: 70px;
    text-align: center;
    font-size: large;
    background-color: white;
    border-width: 10px;
    border-radius: 10px;
}
</style>

