<template>
    <div class="logo">
        <img src="@/assets/img/school_logo.png" style="width: 200px;" title="logo">
        <div class="headtext">《主题》</div>
    </div>
    <div class="members">
        <h3 @click="flodAll()" style="margin-left: 2em;">小组成员</h3>
        <div v-for="way, index in contacts" :key="index">
            <h4 class="contact" @click="flod(way)" style="font-weight: 800;">{{ way.name }} : {{ way.id }}</h4>
            <Transition name="slide-fade">
                <div v-show="way.show">
                    <div class="content" style="font-weight: 500;">{{ way.content }}</div>
                </div>
            </Transition>
        </div>
    </div>
</template>

<script>
import { ref } from 'vue'
export default {
    name: 'SideBar',
    setup() {
        function flod(way) {
            way.show = !way.show
        }
        function flodAll() {
            let isFloded = false
            for (let i = 0; i < contacts.value.length; i++){
                if (contacts.value[i].show === true){
                    isFloded = true
                    break
                }
            }
            if (isFloded){
                for (let i = 0; i < contacts.value.length; i++) {
                    contacts.value[i].show = false
                }
            }
            else{
                for (let i = 0; i < contacts.value.length; i++) {
                    contacts.value[i].show = true
                }
            }
        }

        let contacts = ref([
            {
                name: '魏程浩',
                id: '162210118',
                content: "SomeThing",
                show: false,
            },
            {
                name: '黄佩全',
                id: '162210125',
                content: "SomeThing",
                show: false,
            },
            {
                name: '池明轩',
                id: '162210219',
                content: "SomeThing",
                show: false,
            },
            {
                name: '徐子新',
                id: '162210116',
                content: "SomeThing",
                show: false,
            },
            {
                name: '赵璟烨',
                id: '162250208',
                content: "SomeThing",
                show: false,
            },
        ])

        return {
            flod,
            flodAll,

            contacts,
        }
    }
}
</script>

<style scoped>
@font-face {
    font-family: kai;
    src: url('@/fonts/SimKai.ttf');
}

.logo {
    display: block;
    width: 90%;
    margin-top: 5%;
    text-align: center;
}

.members {
    margin-top: 10%;
}

.headtext {
    font-size: 150%;
    font-weight: bolder;
    font-family: kai;
    text-align: center;
}

.contact {
    margin-left: 2em;
    height: 0.5em;
}

.content{
    margin-left: 1em;
    margin-right: 1em;
    word-wrap:break-word;
    word-break: break-all;
}

.el-menu-style:not(.el-menu--collapse) {
    width: 100%;
    min-height: 60%;
}
.slide-fade-enter-active {
    transition: all 0.8s ease-in;
}

.slide-fade-leave-active {
    transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter-from,
.slide-fade-leave-to {
    transform: translateX(20px);
    opacity: 0;
}
</style>